@media only screen and (max-width: 500px) {

  .main-header {
    margin-bottom: 15px;
    height: 40vh;
  }

  .no-cover.main-header {
    height: 30vh;
  }

  .archive-template .main-header {
    max-height: 20vh;
    min-height: 160px;
    padding: 10% 0;
  }

  .main-nav {
    padding: 5px;
    margin-bottom: 2rem;
  }

  .blog-logo {
    padding: 5px;
  }

  .blog-logo img {
    height: 30px;
  }

  .menu-button {
    padding: 0 5px;
    border-radius: 0;
    border-color: transparent;
    color: #2e2e2e;
    background: transparent;

    &:hover {
      color: #2e2e2e;
      border-color: transparent;
      background: none;
    }
  }
  body.nav-opened .menu-button {
    background: none !important;
    border: transparent !important;
  }

  .main-nav.overlay a:hover {
    color: #fff;
    border-color: transparent;
    background: transparent;
  }

  .no-cover {
    .main-nav.overlay {
      background: none;
    }

    .main-nav.overlay .menu-button {
      border: none;
    }
  }

  .main-nav.overlay .menu-button {
    border-color: transparent;
  }

  .nav-title {
    top: 25px;

  }

  .nav-close {
    position: absolute;
    top: 18px;
  }

  .nav ul {
    padding: 60px 9% 5%;
  }

  .inner,
  .pagination {
    width: auto;
    margin: 2rem auto;
  }

  .post {
    width: auto;
    margin: 2rem 16px;
    padding-bottom: 2rem;
    line-height: 1.65em;
  }

  .post-date {
    display: none;
  }

  .post-template {
    .post-header {
      margin-bottom: 2rem;
    }

    .post-date {
      display: inline-block;
    }
  }

  hr {
    margin: 1.75em 0;
  }

  p, ul, ol, dl {
    font-size: 0.95em;
    margin: 0 0 2.5rem 0;
  }

  .page-title {
    font-size: 3rem;
  }

  .post-excerpt p {
    font-size: 0.85em;
  }

  .page-description {
    font-size: 1.6rem;
  }

  h1, h2, h3,
  h4, h5, h6 {
    margin: 0 0 0.3em 0;
  }

  // post header when using cover image
  .main-header.post-head:not(.no-cover) {
    .main-header-content h1 {
      font-size: 3.5rem;
    }
  }

  h1 {
    font-size: 2.8rem;
    letter-spacing: -1px;
  }

  h2 {
    font-size: 2.4rem;
    letter-spacing: 0;
  }

  h3 {
    font-size: 2.1rem;
  }

  h4 {
    font-size: 1.9rem;
  }

  h5 {
    font-size: 1.8rem;
  }

  h6 {
    font-size: 1.8rem;
  }

  body:not(.post-template) .post-title {
    font-size: 2.5rem;
  }

  .post-template {
    .post {
      padding-bottom: 0;
      margin-bottom: 0;
    }

    .site-footer {
      margin-top: 0;
    }
  }

  .post-content img {
    padding: 0;
    width: calc(100% + 32px); /* expand with to image + margins */
    min-width: 0;
    max-width: 112%; /* fallback when calc doesn't work */
  }

  .post-meta {
    font-size: 1.3rem;
    margin-top: 1rem;
  }

  .post-footer {
    padding: 5rem 0 3rem 0;
    text-align: center;

    .author {
      margin: 0 0 2rem 0;
      padding: 0 0 1.6rem 0;
      border-bottom: #EBF2F6 1px dashed;
    }

    .share {
      position: static;
      width: auto;

      a {
        margin: 1.4rem 0.8rem 0 0.8rem;
      }
    }
  }

  .author-meta {
    li {
      float: none;
      margin: 0;
      line-height: 1.75em;

      &:before {
        display: none;
      }
    }
  }

  .older-posts,
  .newer-posts {
    position: static;
    margin: 10px 0;
  }

  .page-number {
    display: block;
  }

  .site-footer {
    margin-top: 3rem;
  }

  .author-profile {
    padding-bottom: 2rem;
  }

  .tag-head.main-header,
  .author-head.main-header {
    height: 20vh;
  }

  .author-profile {
    .author-image {
      margin-top: -70px;
    }

    .author-meta span {
      font-size: 1.4rem;
    }
  }

  .archive-template .main-header .page-description {
    display: none;
  }

}
